@use "../../scss/" as *;
$body-bg: #3bceac;
$body-sd-bg: #ffd23f;
$text-highlight-color: #ee4266;
$text-normal-color: #0c0f0a;
$text-third-color: #540d6e;
$border-radius: 4px;
$shadow: 4px 4px 0px 0px $text-highlight-color;
//540d6e
//0ead69

.body-all {
    //-webkit-print-color-adjust: exact;
    width: 100%;
    margin: auto;
    background-color: $body-bg;
}

.the-section {
    width: 60%;
    margin: auto;
}

.the-personel {
    height: 100vh;
    background-color: $body-sd-bg;
    //border-bottom: 6px solid $text-highlight-color;
    -webkit-box-shadow: 8px 8px 0px 0px $text-highlight-color;
    box-shadow: 8px 8px 0px 0px $text-highlight-color;
    margin-bottom: 8px;
}

.the-footer {
    background-color: $body-sd-bg;
    text-align: center;
    padding-top: 32px;
    padding-bottom: 32px;
}

.left-column {
    /* Style for the left column */
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

.right-column {
    /* Style for the right column */
}

.the-experience-cell {
    margin-bottom: 32px;
}

.the-experiences {
    margin-top: 16px;
}

.the-grid-container {
    display: grid;
    grid-template-columns: repeat(6, 1fr); /* 5 columns with equal width */
    gap: 16px; /* Gap between grid items */
    padding: 20px; /* Padding for the grid container */
}

.the-grid-pr {
    grid-template-columns: repeat(4, 1fr);
}

.the-grid-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: $body-sd-bg;
    padding: 16px;
    text-align: center;
    aspect-ratio: 1;
    overflow: hidden;
    -webkit-box-shadow: $shadow;
    box-shadow: $shadow;
    border: 2px solid $text-third-color;
    border-radius: $border-radius;
}

.the-page {
    height: 100vh;
}

.the-icon {
    aspect-ratio: 1;
    width: 32px;
}

.the-logo {
    aspect-ratio: 1;
    border-radius: $border-radius;
    width: 50%;
}

.the-project-logo {
    aspect-ratio: 1;
    border-radius: $border-radius;
    width: 50%;
}

.the-button {
    margin-top: 16px;
    padding: 10px 16px 10px 16px;
    border: 2px solid $text-third-color;
    width: fit-content;
    -webkit-box-shadow: 4px 4px 0px 0px $text-highlight-color;
    box-shadow: 4px 4px 0px 0px $text-highlight-color;
    background-color: $body-bg;
    border-radius: $border-radius;
}

.the-button:hover {
    background-color: $body-bg;
}

.the-button:active {
    background-color: $body-bg;
    box-shadow: 0px 0px 0px 0px $text-highlight-color;
    transform: translateY(4px);
}

.the-popup-bg {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    z-index: 9999;
    display: flex;
    justify-content: center;
    align-items: center;
}

.the-popup-content {
    background-color: $body-bg;
    padding: 16px;
    border-radius: $border-radius;
    border: 2px solid $text-third-color;
    -webkit-box-shadow: $shadow;
    box-shadow: $shadow;
    max-width: 50%;
    max-height: 70%;
}

@media only screen and (max-width: 600px) {
    .the-section {
        width: 100%;
        padding: 24px;
    }

    .left-column {
        /* Style for the left column */
        width: 100%;
    }

    .the-grid-container {
        display: grid;
        grid-template-columns: repeat(4, 1fr); /* 5 columns with equal width */
        gap: 14px; /* Gap between grid items */
        padding: 10px; /* Padding for the grid container */
    }

    .the-grid-item {
        padding: 8px;
    }

    .grid-pr {
        grid-template-columns: repeat(1, 1fr);
    }

    .the-popup-content {
        max-width: 80%;
        max-height: 70%;
    }

    .grid-title {
        display: none;
    }

    .the-logo {
        width: 70%;
    }

    .the-project-logo {
        width: 40%;
    }
}

@media only screen and (min-width: 601px) and (max-width: 1366px) {
    .grid-title {
        display: none;
    }

    .the-section {
        width: 80%;
    }

    .the-logo {
        width: 100%;
    }

    .the-project-logo {
        width: 50%;
    }

    .grid-pr {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media only screen and (min-width: 1367px) {
    .two-column-layout {
        display: grid;
        grid-template-columns: 1fr 1fr; /* Two columns of equal width */
        gap: 20px; /* Adjust the gap between columns */
    }
}

.the-text-name {
    font-size: 48px;
    color: $text-highlight-color;
    line-height: 1;
    font-weight: 700;
}

.the-text-iam {
    font-size: 32px;
    color: $text-normal-color;
    line-height: 1;
}

.the-text {
    font-size: 18px;
    color: $text-normal-color;
    line-height: 1.5;

    .title {
        font-size: 24px;
    }

    .heading {
        font-size: 20px;
    }

    .highlight {
        color: $text-highlight-color;
    }

    .bold {
        font-weight: 700;
    }

    .italic {
        font-style: italic;
    }
}
