$main-color: #3bceac;
$sd-color: #ffd23f;
$thu-color: #ee4266;
$third-color: #540d6e;
.kk-bg {

}

.kk-bottom-frame {
    background-color: $main-color;
    border: 2px solid $third-color;
    width: fit-content;
    height: fit-content;
    border-radius: 10px;
    padding: 1px;
    padding-bottom: 6px;
}

.kk-inner-frame {
    background-color: $main-color;
    border: 2px solid $third-color;
    width: fit-content;
    -webkit-box-shadow: 0px 4px 0px 0px $thu-color;
    box-shadow: 0px 4px 0px 0px $thu-color;
    margin-top: -4px;
    border-radius: 10px;
    padding: 8px 16px;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    -webkit-user-select: none; /* Safari */
    -ms-user-select: none; /* IE 10 and IE 11 */
    user-select: none; /* Standard syntax */
}

.kk-inner-frame:hover {
    background-color: $main-color;
}

.kk-inner-frame:active {
    background-color: $main-color;
    box-shadow: 0px 0px 0px 0px $thu-color;
    -webkit-box-shadow: 0px 0px 0px 0px $thu-color;
    transform: translateY(4px);
}